@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
    background-image: url(/static/media/header.1d382fd2.jpg);
    width: 100%;
    height: 400px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
}

.light-bg {
    background: '#25252d96',
}
.list{
    width: 100%;
    padding: 0;
}
.list div {
    width: 100%;
    color: white;
    padding: 1rem 0;
}

.list .select{
    background: #404146;
}

.leftSection{
    background: #25252d96;
    height:100vh;
}

.fontColor {
    color:#3dc3e8
}
.borderNone {
    border: none;
}
.biography{
    background-color: #25252d;
    color:white;
    padding: 1.2rem;
}
.popularity{
    padding:1rem 0;
}
.popularity label{
    color:white;
    padding-right:5px;
    font-size: 1.8rem;
}
.popularity input{
    margin-top: 16px;
    width:100%;
}
.name{
    font-size: 2.2rem;
    color: white;
}

.move-top {
    position: relative;
    top: -60px;
}

.popularity{
    margin-left: 1rem;
}
.main{
    box-shadow: -2px 38px 47px 126px rgba(0,0,0,0.54);
    -webkit-box-shadow: -2px 38px 47px 126px rgba(0,0,0,0.54);
    -moz-box-shadow: -2px 38px 47px 126px rgba(0,0,0,0.54);
}
body {
  background:#2e2e36;
  overflow: hidden;
  font-family: 'Roboto', sans-serif !important;
}
